import React from 'react';
import Home from './components/home/Home';
import JsonFormatter from './components/json/JsonFormatter/JsonFormatter';
import JsonValidator from './components/json/JsonValidator/JsonValidator';
import logo from './logo.png';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Links from './components/home/Links';
import PageNotFound from './components/home/PageNotFound';

function App() {
  let state = {
    "url": "https://datatoolsonline.com"
  }
  return (
    <div className="App">
      <header className="shadow App-header">
        <div className="main-navbar-brand">
          <nav id="navbar" className="navbar navbar-light bg-light">
            <a href="./">
              <img src={logo} height="40" className="d-inline-block align-top" alt="DataTools Online" />
            </a>
          </nav>
        </div>
      </header>

      <section>
        <BrowserRouter>
          <Switch>
            <Route exact={true} path="/" component={Home} />
            <Route exact={true} path="/json-formatter" render={(props) => <JsonFormatter {...props} url={state.url} />} />
            <Route exact={true} path="/json-validator" component={JsonValidator} />
            <Route component={PageNotFound} />
          </Switch>
        </BrowserRouter>
      </section>
      <section>
        <Links/>
      </section>
    </div>
  );
}

export default App;
